.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: rgb(83, 77, 77);
  height: 100vh;
}

.buttonsContainer {
  display: flex;
  flex-direction: column;
  width: 300px;
}

.button {
  background-color: green;
  height: 100px;
  margin-bottom: 20px;
  color: antiquewhite;
  font-size: 42px;
}

.vienas {
  background-color: rgb(143, 219, 143);
}

.backContainer {
  display: flex;
  width: 100%;
  justify-content: flex-start;
}

.backButton {
  width: 200px;
  height: 40px;
  margin-bottom: 15px;
}

.block {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
  background-color: aquamarine;
}

.blokas1 {
  background-color: skyblue;
}

.blokas2 {
  background-color: red;
}

.blokas3 {
  background-color: green;
}

.blokas4 {
  background-color: yellow;
}

.blokas5 {
  background-color: rgb(68, 22, 22);
}

.zaidimoTurinys {
  display: flex;
  width: 100%;
  height: 100%;
}

.zaidimoKonteineris {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.zaidimoEkranas {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  background-color: antiquewhite;
  margin: 0 10px 10px 0;
}

.kvadratelis {
  width: 20px;
  height: 20px;
  border: 1px solid black;
}

.clicked {
  background-color: red;
}

.blockKonteineris {
  padding: 50px 10px;
}
